<template>
  <div>
    <canvas ref="canvasRef" :width="sizeRef" :height="sizeRef"></canvas>
  </div>
</template>

<script lang="ts">
import { defineComponent, nextTick, onMounted, ref, watch } from 'vue';
import { FrameColorSegmentType } from '@/types/frame-color-segment.type';
import { useCanvasSaver } from '@/composables/useCanvasSaver';
import { useStore } from 'vuex';

export default defineComponent({
  name: 'AppEditorCanvas',
  props: {
    segments: { type: Array as () => FrameColorSegmentType[], required: true },
    imageUrl: { type: [String, null], default: null },
  },
  emits: ['update:imageUrl'],
  setup(props, { emit }) {
    const store = useStore();
    const canvasRef = ref<HTMLCanvasElement | null>(null);
    const imageRef = ref<HTMLImageElement | null>(null);
    const originalUrlRef = ref<string | null>(null);
    const thicknessRef = ref(90);
    const sizeRef = ref(480);
    const rotationRef = ref(0); //  
    const startAngleRef = ref<number | null>(null); //   , ,      12 
    const offsetXRef = ref(0); //     X
    const offsetYRef = ref(0); //     Y

    let isLoadImage = false;
    const render = async () => {
      if (!canvasRef.value) return;
      const ctx = canvasRef.value.getContext('2d')!;
      ctx.clearRect(0, 0, sizeRef.value, sizeRef.value);
      if (props.imageUrl && !isLoadImage) {
        imageRef.value = await loadImage(props.imageUrl);
        isLoadImage = true;
      }
      drawBaseImage(ctx);
      drawFrame(ctx);
    };

    const { getCanvasSrc, saveImage, loadImage, resetImage, zoomPlus, zoomMinus, zoom } =
      useCanvasSaver(canvasRef, render, emit);

    const getCanvasValue = () => {
      return canvasRef.value;
    };

    const getImageSrc = () => {
      return getCanvasSrc();
    };

    const triggerSaveImage = () => {
      saveImage();
    };

    const drawBaseImage = (ctx: CanvasRenderingContext2D) => {
      const radius = sizeRef.value / 2; //   ,    
      const thickness = thicknessRef.value; //   (  )
      const img = imageRef.value;
      if (!img) return;

      ctx.save(); //    
      ctx.beginPath(); //      
      //         ,   
      ctx.arc(radius, radius, radius - thickness, 0, 2 * Math.PI);
      ctx.closePath(); //  
      ctx.clip(); //     (    )
      //       +  
      ctx.translate(radius + (offsetXRef.value ?? 0), radius + (offsetYRef.value ?? 0));
      ctx.rotate((rotationRef.value * Math.PI) / 180); //      (   )

      const baseSize = sizeRef.value - thickness * 2; //     (   )
      const minSide = Math.min(img.width, img.height); //     (   )
      const targetSize = baseSize * zoom.value; //     
      //  :
      // -        minSide  minSide
      // -     
      // -   targetSize  targetSize
      ctx.drawImage(
        img,
        (img.width - minSide) / 2, // x- 
        (img.height - minSide) / 2, // y- 
        minSide, //  
        minSide, //  
        -targetSize / 2, // x-  
        -targetSize / 2, // y-  
        targetSize, //   
        targetSize //   
      );
      //    
      ctx.restore();
    };

    const drawFrame = (ctx: CanvasRenderingContext2D) => {
      const radius = sizeRef.value / 2; //    (  )
      const thickness = thicknessRef.value; //  ,      
      const step = (2 * Math.PI) / props.segments.length; //    ( ),  2 /  
      //    (      ,   -/2)
      const startOffset = startAngleRef.value ?? -Math.PI / 2;
      ctx.lineWidth = thickness; //      
      //        
      props.segments.forEach((seg, i) => {
        ctx.beginPath(); //      
        ctx.strokeStyle = seg.color; //      
        const start = i * step + startOffset; //       i
        const end = (i + 1) * step + startOffset; //       i
        //         
        //  : (radius, radius)
        // : radius - thickness / 2 (     )
        ctx.arc(radius, radius, radius - thickness / 2, start, end);
        ctx.stroke(); //    
      });
    };

    onMounted(render);
    watch(
      () => props.imageUrl,
      (url) => {
        if (url && !originalUrlRef.value) {
          originalUrlRef.value = url;
        }

        if (url && originalUrlRef.value) {
          isLoadImage = false;
          zoom.value = 1;
          originalUrlRef.value = url;
        }

        render();
      }
    );

    watch(
      () => store.getters['mobile/clientWidth'],
      (value) => {
        if (value < 600) {
          thicknessRef.value = 60;
          sizeRef.value = 320;
          nextTick(() => render());
          return;
        }

        if (value < 1024) {
          thicknessRef.value = 70;
          sizeRef.value = 400;
          nextTick(() => render());
          return;
        }

        if (value > 1024) {
          thicknessRef.value = 90;
          sizeRef.value = 520;
          nextTick(() => render());
          return;
        }
      },
      { immediate: true }
    );

    watch(
      () => props.segments,
      () => {
        nextTick(() => render());
      }
    );

    return {
      canvasRef,
      resetImage,
      triggerSaveImage,
      zoomPlus,
      zoomMinus,
      sizeRef,
      getCanvasValue,
      getImageSrc,
    };
  },
});
</script>
<style scoped></style>
