export function tokenizeTitle(title: string): string[] {
  const result: string[] = [];
  const words = title.toLowerCase().split(/\s+/); //   

  for (const word of words) {
    //  
    for (let i = 1; i <= word.length; i++) {
      result.push(word.substring(0, i));
    }

    //  
    for (let i = 0; i < word.length; i++) {
      result.push(word[i]);
    }
  }

  return result;
}
